<template>
  <div class="auth-page">
    <div class="tips">请尽快完成身份认证，此决定后续佣金发放归属，一旦认证不能更换，身份认证信息必须与注册手机号身份信息一致。</div>
    <div class="auth-form">
      <div class="auth-item-input">
        <div class="auth-label">绑定手机号</div>
        <div class="auth-input">{{getUserInfo.phone}}</div>
      </div>
      <div class="auth-item-input">
        <div class="auth-label">证件类型</div>
        <div class="auth-input">身份证</div>
      </div>
      <div class="auth-item-input">
        <div class="auth-label">姓名</div>
        <div class="auth-input"><input type="text" placeholder="填写您的真实姓名" v-model="name" @input="inputFn"></div>
      </div>
      <div class="auth-item-input">
        <div class="auth-label">证件号</div>
        <div class="auth-input"><input type="text" placeholder="填写对应真实名称的身份证号" v-model="usercode" @input="inputFn"></div>
      </div>
    </div>
    <div class="auth-btn" :class="{'btn-disable': !canSubmit}">
      <a href="javascript:;" @click="doSure">确定</a>
    </div>
  </div>
</template>

<script>
import util from '@/utils/utils'
import URL from '@/http/url'
import { mapGetters } from 'vuex';

export default {
  name: 'trueAuthentication',
  data() {
    return {
      name: '',
      usercode: '',
      canSubmit: false,
      destination: undefined, // 成功后跳转目标地址
    }
  },
  computed: {
    ...mapGetters(['getUserInfo'])
  },
  mounted() {
    // console.log(this.getUserInfo);
    this.destination = this.$route.query ? this.$route.query.destination : undefined
  },
  methods: {
    inputFn() {
      if (!this.name || !this.usercode) {
        this.canSubmit = false
      } else {
        this.canSubmit = true
      }
    },
    doSure() {
      if (util.isIdCard(this.usercode)) {
        let url = `${URL.POST_USERDATAANDID}?realName=${this.name}&identityNo=${this.usercode}&mobile=${this.getUserInfo.phone}&channal=APP`
        this.$http.post(url).then(res => {
          let result = res.data;
          if (result.data) {
            this.$toast('认证成功')
            let timer = setTimeout(() => {
              clearTimeout(timer)
              if (!this.destination) {
                if (window.location && window.location.href.indexOf('sg_rn_app') !== -1) {
                  this.$router.replace('/bindcard?sg_rn_app')
                } else {
                  this.$router.replace('/bindcard')
                }
              } else {
                this.$router.replace(this.destination)
              }
            }, 2600)
          } else {
            this.$toast(result.message)
          }
        })
      } else {
        this.$toast('身份证格式不正确')
      }
    }
  }
}
</script>

